import React, { memo, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';

const HEIGHT = 50;
const SongWaveform = memo(({waveform, width=340}) => { 

    if (!waveform) {
        return '';
    }

    let parts = [];
    for (let i=0; i < waveform.length - 4; i+=4) {
        let height = Math.pow(waveform[i], 0.25)*16;
        let p = {
            height,
            y: HEIGHT/2 - height/2,
            r: waveform[i+1],
            g: waveform[i+2],
            b: waveform[i+3]
        };
        parts.push(p);
    }
    return (
        <svg
          width={width}
          height={HEIGHT}
          className="song-waveform">
          {parts.map((part, i) => <rect x={i*(width/parts.length)} y={part.y} width={2} height={part.height} fill={`rgb(${part.r}, ${part.g}, ${part.b})`}/>)}
        </svg>
    );
});

export default SongWaveform;
